  font-weight: 300;
}

body {
  --color-text: #3d4e61;
  --color-bg: #fff;
  font-family: Avenir, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 300;
}

.big__form {
  margin-top: 20px;
  display: block;
  position: relative;
}

.icon {
  display: block;
  position: absolute;
}

.icon--big {
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.input--big {
  height: 70px;
  margin-bottom: 20px;
